
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Msg from 'vux/src/components/msg/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import * as util from '../api/common'
import axios from 'axios'

export default {
directives: {
TransferDom
},
components: {
Msg, Popup, Confirm
},
data () {
return {
isShowOk: false,
bill_id: '',
content: '',
refund_file: '',
imgs: [],
imgData: {
accept: 'image/gif, image/jpeg, image/png, image/jpg'
},
uploadImgUrl: ''
}
},
methods: {
onItemClick () {

},
openFile () {




const event = new MouseEvent('click')
this.$refs.file.dispatchEvent(event)
},
addImg (event) {
let url = util.baseURL + 'shopeeker/upload'
let self = this
let img1 = event.target.files[0]
let type = img1.type 
let size = img1.size 
if (self.imgData.accept.indexOf(type) === -1) {
alert('请选择我们支持的图片格式！')
return false
}




let file = new FormData()
file.append('file', img1)
file.append('type', type)

self.$axios.post(url, file, {
headers: {'Content-Type': 'multipart/form-data'}
}).then(res => {
self.uploadImgUrl = res.data.pic
console.log('上传结果：', res.data.pic)
self.clearfile()
})
},
delectImg () {

var self = this
let params = new URLSearchParams()
params.append('file', self.uploadImgUrl) 
var instance = axios.create({ baseURL: util.baseURL })

const _this = this 
self.$vux.confirm.show({
content: '确定删除当前图片吗？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('shopeeker/fileDelete', params).then(res => {
if (res.data.code === 200) {
self.$vux.toast.text(res.data.message)
self.uploadImgUrl = ''
}
})
}
})
},
showImgUrl (url) {
return util.imgShowURL + url
},
clearfile () {
this.$refs.file.value = ''
},
submit () {
var self = this
let params = new URLSearchParams()
params.append('bill_id', util.getQueryParam('id')) 
params.append('content', self.content) 
params.append('refund_file', self.uploadImgUrl) 
var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('buyer/center/billfileSubmit', params).then(res => {
console.log('res', res.data)
if (res.data.code === 200) {
self.$vux.toast.text(res.data.message)
self.uploadImgUrl = ''
self.isShowOk = true
} else {
self.$vux.toast.text(res.data.message)
}
})
}
}
}



